	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/01/crh0125.jpg\" height=\"361\" width=\"350\" border=\"0\" alt=\"crh0125.jpg: \" /></p>\n<p>We're starting to notice advances in Christopher's development. He now seems able to focus on the colored blocks we hung over his cradle. He's gaining control of his arms and hands, although they aren't quite coordinated yet. And he's started smiling (really smiling, not just the instinctive grin when he's just finished eating).</p>\n<p>Grandma Ruth is coming again tomorrow for a couple days. Grandma Shirley and Grandpa Marlyn have their plane tickets for the end of March. It looks like we'll have the baptism on 2 April in Jesteburg.</p>\n<p>We've gotten a lot of compliments on Christopher's sweater. It was knitted by Aunt Kirsten for her son Jakob, and we have it on loan.</p>","fields":{"title":"A World Full of Colors","date":"25 Jan 2000","url":"https://papascott.de/2000-01-25-a-world-full-of-colors/"}}},"pathContext":{"slug":"/2000-01-25-a-world-full-of-colors/","prev":{"fields":{"title":"Monday Monday","date":"24 Jan 2000","slug":"/2000-01-24-monday-monday/"}},"next":{"fields":{"title":"Yet Another One","date":"26 Jan 2000","slug":"/2000-01-26-yet-another-one/"}}}}

